nav{
    display:flex;
    justify-content: space-around;
    align-items: center;
    background-color: brown;
    padding:10px 0;
    position:fixed;
    top: 0;
    width: 100%;
    
}
nav > h1
{
    font-size:30px;
    color:white;
    font-weight:500;
}

nav ul{
    display:flex;
    justify-content: space-around;
    align-items: center;
    width:30%;
   
}
nav ul a{
    font-size:20px;
    font-weight:400;
    color:white;
    text-decoration:none;
}
nav ul a:hover{
    font-weight:500;
}