*
{
    margin:0;
    padding:0;
}
.home
{   
    position: fixed;
    left: 120px;
    display:flex;
    flex-direction:column;
    justify-content: center;
    align-items: center;
    width: 100%;
    max-width:1000px;
    margin:0 auto;
    margin-top:90px;
    height:auto;


}
.home__container
{
   
    display:flex;
    flex-direction:column;
    justify-content: center;
    align-items: center;
}
.home__formContainer {
    box-shadow:1px 4px 12px -2px rgba(0,0,0,4.3);

    background:burlywood;
    padding:30px;
    border-radius:10px;
    width:70%;
}
.home__formContainer input{

    border:1px solid brown;
    outline:none;
    padding:15px 10px;
    width:32%;
    border-radius:8px;
    margin:10px 40px;
    background:transparent;
    color:brown;
    font-size:14px;
}
.home__formContainer label{
    border:1px solid brown;
    outline:none;
    padding:13px 110px;
    width:300px;
    border-radius:8px;
    margin:10px 10px;
    background:transparent;
    color:brown;
    font-size:14px;
}
.home__formContainer input::placeholder
{
    color:brown;
}
.home__formContainer select{
    border:1px solid brown;
    outline:none;
    padding:15px 10px;
    width:35%;
    border-radius:8px;
    margin:10px 40px;
    background:transparent;
    color:brown;
    font-size:14px;
}
.home__formContainer button{


background: brown;
border:0;
outline:none;
font-size:18px;
color:white;
padding:10px 30px;
margin-top:20px;
margin-left: 250px;
border-radius:12px;
cursor:pointer;
}
.home__formContainer button:hover{
background: transparent;
border:1px solid brown;
outline:none;
font-size:18px;
color:brown;
padding:10px 30px;
margin-top:20px;
margin-left: 250px;
border-radius:12px;
cursor:pointer;
transition: 0.3s;
} 
.home__title{
    text-align: center;

}

.id{
    display: none;
}