.user{
    box-shadow: 10px 8px 15px -2px rgba(135, 135, 135, 0.37);
    background-color:bisque;
    margin-top: 10px;
    margin-bottom:30px;
    min-width:600px;
    padding:20px;
    border-radius: 10px;
    

    
    }
    .user h2,.user h3{
        color:rgba(0,0,0,0.541);
        display: flex;
        justify-content: start;
        align-items: center;
        margin-left: 50px;
    }
    .user span
    {
    margin-left: 10px;
    font-weight:500;
    color:black;
    }
    .user__bottom
    {
    margin-top:20px;
    }
    .user__delete
    {
    
    border:none;
    outline:none;
    padding:10px;
    width:100px;
    background-color:red;
    border-radius:8px;
    color:white;
    cursor:pointer;
    }
    table{
        border-collapse: collapse;
        width: 100%;
        text-align: center;
    }
    table td, table th{
        /* border: 1px solid #ddd; */
        padding: 8px;
    }